import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

/* @component: Square
   fully controlled by <Square>
   */
function Square(props) {
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      squares: Array(this.props.rowSize * this.props.rowSize).fill(null),
      xIsNext: true
    };
  }

  handleClick(i) {
    const squares = this.state.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      squares: squares,
      xIsNext: !this.state.xIsNext
    });
  }

  renderSquare(i) {
    return <Square key={i} value={this.state.squares[i]} onClick={() => this.handleClick(i)} />;
  }

  /**
    Build a board of rowSize x rowSize. squares are numbered from 0 -> (rowSize^^2)-1
    We create each row[] and push to rows[]
  */
  BuildBoard(rowSize) {
    const rows = [];
    for (let y = 0; y < rowSize; y++) {
      let row = [];
      for (let x = 0; x < rowSize; x++) {
        row.push(this.renderSquare(x + y * rowSize));
      }
      rows.push(row);
    }
    return rows;
  }

  render() {
    const winner = calculateWinner(this.state.squares);
    let status;
    if (winner) {
      status = "Winner: " + winner;
    } else {
      status = "Next player: " + (this.state.xIsNext ? "X" : "O");
    }

    // Draw the rowSize x rowSize board
    const rows = this.BuildBoard(this.props.rowSize);

    return (
      <div>
        <div className="status">{status}</div>
        {rows.map(r => (
          <div className="board-row">{r}</div>
        ))}
      </div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="game">
        <div className="game-board">
          <Board rowSize={19} />
        </div>
        <div className="game-info">
          <div>{/* status */}</div>
          <ol>{/* TODO */}</ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

function calculateWinner(squares) {
  // This is tictactoe logic only
  const lines = [[0, 1, 2], [3, 4, 5], [6, 7, 8], [0, 3, 6], [1, 4, 7], [2, 5, 8], [0, 4, 8], [2, 4, 6], [20, 21, 22]];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}
